* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: none;
  list-style: none;
  word-wrap: break-word;
}

body {
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
    line-height: 26px;
    font-weight: 400;
    letter-spacing: 0.5px;
    color: #fff;
    overflow-x: hidden;
    height: 100%;
    background-color: #000;
}
h1,
h2,
h3,
h4,
h5,
h6{
	font-weight: 600;
}
img{
	max-width: 100%;
	height: auto;
}
ul{
	padding: 0;
	list-style: none;
}
p {
    font-weight: 400;
	font-family: 'Poppins', sans-serif;
    margin: 0;
    font-size: 15px;
    letter-spacing: 0;
}
a:hover{
	text-decoration: none;
	outline: none;
}
.text_white{
	color: #fff;
}
.padding_0{
	padding: 0;
}
.gray_bg{
	background: #fff;
}
.dark_bg{
	background: #000;
}
/* START PRELOADER DESIGN */
.preloader {
	background: #fff;
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 99999;	
}
.spinner {
	width: 50px;
	height: 50px;
	position: absolute;
	top: 50%;
	left: 50%;
	margin: -25px 0 0 -25px;
	font-size: 10px;
	text-indent: -12345px;
	z-index: 10000;
}

.double-bounce1, .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #ffb400 ;
  opacity: 0.6;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=60)";
  position: absolute;
  top: 0;
  left: 0;
  
  -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
  animation: sk-bounce 2.0s infinite ease-in-out;
}
.dark_preloader .double-bounce1, .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #fff;
  opacity: 0.6;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=60)";
  position: absolute;
  top: 0;
  left: 0;
  
  -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
  animation: sk-bounce 2.0s infinite ease-in-out;
}

.double-bounce2 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}

@-webkit-keyframes sk-bounce {
  0%, 100% { -webkit-transform: scale(0.0) }
  50% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bounce {
  0%, 100% { 
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
  } 50% { 
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
  }
}
/* END PRELOADER DESIGN */

/* Start Section-Padding */
.section_padding{
	padding: 100px 0;
}
/* End Section-Padding */

/* Start Section-Heading */
.section_heading{
    padding-bottom: 100px;
}
.section_heading h2{
    font-size: 35px;
    font-weight: 600;
    color: #222;
    margin-top: 5px;
    margin-bottom: 10px;
}
.section_heading h2 span{
	font-size: 35px;
    font-weight: 600;
	color: #ffb400;
}
.dark_bg .section_heading h2{
	color: #fff;
}
.dark_bg .section_heading h2 span{
	font-size: 35px;
    font-weight: 600;
	color: #ffb400;
}
.section_heading .sm_border {
    display: block;
    height: 2px;
    width: 70px;
    margin-bottom: 15px;
    bottom: -15px;
    position: relative;
    left: 50%;
    background-color: #ffb400;
    -webkit-transform: translate(-50%, 0);
    -ms-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
}
.section_heading span{
	font-weight: 400;
	color: #ffb400;
}
.section_heading p {
    margin-bottom: 20px;
    margin: 0 auto;  
}
.dark_bg .section_heading p{
	color: #fff;
}

/* Start Btn-Style */
.main_btn {
    color: #fff;
    font-size: 13px;
    margin-top: 30px;
    text-transform: capitalize;
    padding: 20px 28px;
    letter-spacing: 0.6px;
	font-weight: 600;
    background: #ffb400;
    line-height: 10px;
	border: none;
    border-radius: 30px;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out 0s;
}
.main_btn:hover {
    color: #ffb400;
    background: #fff;
}
.blog-info .blog_btn {
    padding: 20px 30px;
}
/* ----------------------------------------------------------------
    [ End Buttons ]
-----------------------------------------------------------------*/


/* ----------------------------------------------------------------
    [ 03 Start Header ]
-----------------------------------------------------------------*/
.navbar-default {
    border: none;
    border-radius: 0;
    margin-bottom: 0;
    width: 100%;
    padding: 20px 0;
    background: #fff;
    transition: all 0.4s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;
}
.navbar-brand p{
    font-size: 30px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #ffb400;
	font-family: 'Poppins', sans-serif;
}
.navbar-default .navbar-collapse, .navbar-default .navbar-form {
    border-color: #fff;
}
.menu-top { background: rgba(0, 0, 0, 0) none repeat scroll 0 0 }
.navbar-default .navbar-nav>li>a{
    color: #333333;
    font-size: 15px;
    font-weight: 600;
    text-transform: uppercase;
	transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
}
.navbar-default.dark_bg_menu .navbar-nav>li>a{
	color: #fff !important;
}
.navbar-default.dark_bg_menu .navbar-nav>li>a:hover{
	color: #ffb400 !important;
}
.navbar-default .navbar-nav>li>a:hover,
.navbar-default .navbar-nav>li>a:focus{
    color: #ffb400;
}
.navbar-default .navbar-nav>.active>a, 
.navbar-default .navbar-nav>.active>a:hover, 
.navbar-default .navbar-nav>.active>a:focus{
    color: #333333;
    background-color: transparent;
}
.navbar-default.dark_bg_menu .navbar-nav>.active>a,
.navbar-nav>.active>a:hover, .navbar-nav>.active>a:focus{
	color: #ffb400 !important;
}
.mobile-nav{
	display:none;
}
.navbar-default.menu-shrink {
    background-color: #fff;
    box-shadow: 0 10px 15px rgba(25,25,25,0.1);
    padding: 10px 0;
    width: 100%;
    animation: fadeInDown 1s both 0.2s;
}
.navbar-default.dark_bg_menu.menu-shrink{
	background-color: #000;
    box-shadow: 0 10px 15px rgba(25,25,25,0.1);
    padding: 10px 0;
    width: 100%;
    animation: fadeInDown 1s both 0.2s;
}
.navbar-default.menu-shrink .navbar-brand p{
    color: #ffb400;
}
.navbar-default.menu-shrink li a { 
	color: #333333; 
	transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
}
.navbar-default.dark_bg_menu.menu-shrink li a{
	color: #fff !important; 
	transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
}
.navbar-default.menu-shrink li a:hover { color: #ffb400 !important }
.navbar-default.menu-shrink .navbar-nav>.active>a, 
.navbar-default.menu-shrink .navbar-nav>.active>a:hover,
.navbar-default.menu-shrink .navbar-nav>.active>a:focus{
    color: #ffb400 !important;
    background-color: transparent;
}
.navbar-default .navbar-toggle { background: #fff none repeat scroll 0 0 }
.navbar-default .navbar-toggle:hover,
.navbar-default .navbar-toggle:focus { background-color: #fff }
.navbar-default .navbar-toggle .icon-bar { background-color: #ffb400 }
/* ----------------------------------------------------------------
     [ End Header ]
-----------------------------------------------------------------*/


/* ----------------------------------------------------------------
     [ 04 Start Banner ]
-----------------------------------------------------------------*/
#particles-js{
    position: absolute;
    width: 100%;
    height: 100%;
	top: 0;
}
.main_banner{
	height: 700px;
	background: #fff;
	background-size: cover;
	background-repeat: no-repeat;
}
.dark_bg{
	background: #000;
}
.banner_content{
	margin-top: 170px;
}
.banner_content strong {
	text-transform: uppercase;
    color: #ffb400;
    font-size: 20px;
}
.banner_content h1 {
    color: #222;
    font-size: 50px;
    margin: 15px 0 10px;
}
.dark_bg .banner_content h1{
	color: #fff;
}
.banner_content h1 span .fw_600{
	font-weight: 700;
}
.banner_content h1 span .fw_300{
	font-weight: 700;
}
.banner_content p {
    color: #333333;
    padding-bottom: 0;
}
.dark_bg .banner_content p{
	color: #fff;
}
.banner_social_link{
	margin-top: 35px;
}
.banner_social_link li {
    display: inline-block;
    margin-right: 12px;
}
.banner_social_link li a{
	color: #ffb400;
	border: 1px solid #ffb400;
	width: 50px;
	height: 50px;
	line-height: 50px;
	border-radius: 50%;
	text-align: center;
	display: inline-block;
	font-size: 22px;
	transition: .5s;
}
.dark_bg .banner_social_link li a{
	color: #ffb400;
	border: 1px solid #ffb400;
}
.banner_social_link li a:hover{
	background: #ffb400 ;
	border-color: #ffb400;
	color: #fff;
}
.dark_bg .banner_social_link li a:hover{
	background: #ffb400 ;
	border: 1px solid #ffb400;
	color: #fff;
}
.banner-images img{
	max-width: 100%;
	margin-top: 40px;
}
#round {
    position: absolute;
    top: 75px;
    bottom: 0;
	z-index: 9;
}
/* ----------------------------------------------------------------
    [ End Banner ]
-----------------------------------------------------------------*/


/* ----------------------------------------------------------------
    [ 15 Start Top-control ]
-----------------------------------------------------------------*/
.topcontrol {
    background: #ffb400 none repeat scroll 0 0;
    border-radius: 0;
    bottom: 5px;
    margin-bottom: 80px;
    box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);
    color: #fff;
    cursor: pointer;
    font-size: 20px;
    height: 35px;
    opacity: 1;
    filter: alpha(opacity=100);
    padding: 5px 9px;
    position: fixed;
    margin-right: 10px;
    -webkit-transition: all 0.2s ease 0s;
    transition: all 0.2s ease 0s;
    width: 35px;
    z-index: 99;
}
.topcontrol:hover {
    background: #fff;
    color: #ffb400;
}
