mageTransform.Microsoft.Alpha(Opacity=60)\";\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  \r\n  -webkit-animation: sk-bounce 2.0s infinite ease-in-out;\r\n  animation: sk-bounce 2.0s infinite ease-in-out;\r\n}\r\n\r\n.double-bounce2 {\r\n  -webkit-animation-delay: -1.0s;\r\n  animation-delay: -1.0s;\r\n}\r\n\r\n@-webkit-keyframes sk-bounce {\r\n  0%, 100% { -webkit-transform: scale(0.0) }\r\n  50% { -webkit-transform: scale(1.0) }\r\n}\r\n\r\n@keyframes sk-bounce {\r\n  0%, 100% { \r\n    transform: scale(0.0);\r\n    -webkit-transform: scale(0.0);\r\n  } 50% { \r\n    transform: scale(1.0);\r\n    -webkit-transform: scale(1.0);\r\n  }\r\n}\r\n/* END PRELOADER DESIGN */\r\n\r\n/* Start Section-Padding */\r\n.section_padding{\r\n\tpadding: 100px 0;\r\n}\r\n/* End Section-Padding */\r\n\r\n/* Start Section-Heading */\r\n.section_heading{\r\n    padding-bottom: 100px;\r\n}\r\n.section_heading h2{\r\n    font-size: 35px;\r\n    font-weight: 600;\r\n    color: #222;\r\n    margin-top: 5px;\r\n    margin-bottom: 10px;\r\n}\r\n.section_heading h2 span{\r\n\tfont-size: 35px;\r\n    font-weight: 600;\r\n\tcolor: #ffb400;\r\n}\r\n.dark_bg .section_heading h2{\r\n\tcolor: #fff;\r\n}\r\n.dark_bg .section_heading h2 span{\r\n\tfont-size: 35px;\r\n    font-weight: 600;\r\n\tcolor: #ffb400;\r\n}\r\n.section_heading .sm_border {\r\n    display: block;\r\n    height: 2px;\r\n    width: 70px;\r\n    margin-bottom: 15px;\r\n    bottom: -15px;\r\n    position: relative;\r\n    left: 50%;\r\n    background-color: #ffb400;\r\n    -webkit-transform: translate(-50%, 0);\r\n    -ms-transform: translate(-50%, 0);\r\n    transform: translate(-50%, 0);\r\n}\r\n.section_heading span{\r\n\tfont-weight: 400;\r\n\tcolor: #ffb400;\r\n}\r\n.section_heading p {\r\n    margin-bottom: 20px;\r\n    margin: 0 auto;  \r\n}\r\n.dark_bg .section_heading p{\r\n\tcolor: #fff;\r\n}\r\n\r\n/* Start Btn-Style */\r\n.main_btn {\r\n    color: #fff;\r\n    font-size: 13px;\r\n    margin-top: 30px;\r\n    text-transform: capitalize;\r\n    padding: 20px 28px;\r\n    letter-spacing: 0.6px;\r\n\tfont-weight: 600;\r\n    background: #ffb400;\r\n    line-height: 10px;\r\n\tborder: none;\r\n    border-radius: 30px;\r\n    -webkit-transition: all 0.3s ease-in-out;\r\n    transition: all 0.3s ease-in-out 0s;\r\n}\r\n.main_btn:hover {\r\n    color: #ffb400;\r\n    background: #fff;\r\n}\r\n.blog-info .blog_btn {\r\n    padding: 20px 30px;\r\n}\r\n/* ----------------------------------------------------------------\r\n    [ End Buttons ]\r\n-----------------------------------------------------------------*/\r\n\r\n\r\n/* ----------------------------------------------------------------\r\n    [ 03 Start Header ]\r\n-----------------------------------------------------------------*/\r\n.navbar-default {\r\n    border: none;\r\n    border-radius: 0;\r\n    margin-bottom: 0;\r\n    width: 100%;\r\n    padding: 20px 0;\r\n    background: #fff;\r\n    transition: all 0.4s ease-in-out;\r\n    -webkit-transition: all 0.4s ease-in-out;\r\n}\r\n.navbar-brand p{\r\n    font-size: 30px;\r\n    font-weight: 800;\r\n    text-transform: uppercase;\r\n    letter-spacing: 1px;\r\n    color: #ffb400;\r\n\tfont-family: 'Poppins', sans-serif;\r\n}\r\n.navbar-default .navbar-collapse, .navbar-default .navbar-form {\r\n    border-color: #fff;\r\n}\r\n.menu-top { background: rgba(0, 0, 0, 0) none repeat scroll 0 0 }\r\n.navbar-default .navbar-nav>li>a{\r\n    color: #333333;\r\n    font-size: 15px;\r\n    font-weight: 600;\r\n    text-transform: uppercase;\r\n\ttransition: all 0.3s ease-in-out;\r\n    -webkit-transition: all 0.3s ease-in-out;\r\n}\r\n.navbar-default.dark_bg_menu .navbar-nav>li>a{\r\n\tcolor: #fff !important;\r\n}\r\n.navbar-default.dark_bg_menu .navbar-nav>li>a:hover{\r\n\tcolor: #ffb400 !important;\r\n}\r\n.navbar-default .navbar-nav>li>a:hover,\r\n.navbar-default .navbar-nav>li>a:focus{\r\n    color: #ffb400;\r\n}\r\n.navbar-default .navbar-nav>.active>a, \r\n.navbar-default .navbar-nav>.active>a:hover, \r\n.navbar-default .navbar-nav>.active>a:focus{\r\n    color: #333333;\r\n    background-color: transparent;\r\n}\r\n.navbar-default.dark_bg_menu .navbar-nav>.active>a,\r\n.navbar-nav>.active>a:hover, .navbar-nav>.active>a:focus{\r\n\tcolor: #ffb400 !important;\r\n}\r\n.mobile-nav{\r\n\tdisplay:none;\r\n}\r\n.navbar-default.menu-shrink {\r\n    background-color: #fff;\r\n    box-shadow: 0 10px 15px rgba(25,25,25,0.1);\r\n    padding: 10px 0;\r\n    width: 100%;\r\n    animation: fadeInDown 1s both 0.2s;\r\n}\r\n.navbar-default.dark_bg_menu.menu-shrink{\r\n\tbackground-color: #000;\r\n    box-shadow: 0 10px 15px rgba(25,25,25,0.1);\r\n    padding: 10px 0;\r\n    width: 100%;\r\n    animation: fadeInDown 1s both 0.2s;\r\n}\r\n.navbar-default.menu-shrink .navbar-brand p{\r\n    color: #ffb400;\r\n}\r\n.navbar-default.menu-shrink li a { \r\n\tcolor: #333333; \r\n\ttransition: all 0.3s ease-in-out;\r\n    -webkit-transition: all 0.3s ease-in-out;\r\n}\r\n.navbar-default.dark_bg_menu.menu-shrink li a{\r\n\tcolor: #fff !important; \r\n\ttransition: all 0.3s ease-in-out;\r\n    -webkit-transition: all 0.3s ease-in-out;\r\n}\r\n.navbar-default.menu-shrink li a:hover { color: #ffb400 !important }\r\n.navbar-default.menu-shrink .navbar-nav>.active>a, \r\n.navbar-default.menu-shrink .navbar-nav>.active>a:hover,\r\n.navbar-default.menu-shrink .navbar-nav>.active>a:focus{\r\n    color: #ffb400 !important;\r\n    background-color: transparent;\r\n}\r\n.navbar-default .navbar-toggle { background: #fff none repeat scroll 0 0 }\r\n.navbar-default .navbar-toggle:hover,\r\n.navbar-default .navbar-toggle:focus { background-color: #fff }\r\n.navbar-default .navbar-toggle .icon-bar { background-color: #ffb400 }\r\n/* ----------------------------------------------------------------\r\n     [ End Header ]\r\n-----------------------------------------------------------------*/\r\n\r\n\r\n/* ----------------------------------------------------------------\r\n     [ 04 Start Banner ]\r\n-----------------------------------------------------------------*/\r\n#particles-js{\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n\ttop: 0;\r\n}\r\n.main_banner{\r\n\theight: 700px;\r\n\tbackground: #fff;\r\n\tbackground-size: cover;\r\n\tbackground-repeat: no-repeat;\r\n}\r\n.dark_bg{\r\n\tbackground: #000;\r\n}\r\n.banner_content{\r\n\tmargin-top: 170px;\r\n}\r\n.banner_content strong {\r\n\ttext-transform: uppercase;\r\n    color: #ffb400;\r\n    font-size: 20px;\r\n}\r\n.banner_content h1 {\r\n    color: #222;\r\n    font-size: 50px;\r\n    margin: 15px 0 10px;\r\n}\r\n.dark_bg .banner_content h1{\r\n\tcolor: #fff;\r\n}\r\n.banner_content h1 span .fw_600{\r\n\tfont-weight: 700;\r\n}\r\n.banner_content h1 span .fw_300{\r\n\tfont-weight: 700;\r\n}\r\n.banner_content p {\r\n    color: #333333;\r\n    padding-bottom: 0;\r\n}\r\n.dark_bg .banner_content p{\r\n\tcolor: #fff;\r\n}\r\n.banner_social_link{\r\n\tmargin-top: 35px;\r\n}\r\n.banner_social_link li {\r\n    display: inline-block;\r\n    margin-right: 12px;\r\n}\r\n.banner_social_link li a{\r\n\tcolor: #ffb400;\r\n\tborder: 1px solid #ffb400;\r\n\twidth: 50px;\r\n\theight: 50px;\r\n\tline-height: 50px;\r\n\tborder-radius: 50%;\r\n\ttext-align: center;\r\n\tdisplay: inline-block;\r\n\tfont-size: 22px;\r\n\ttransition: .5s;\r\n}\r\n.dark_bg .banner_social_link li a{\r\n\tcolor: #ffb400;\r\n\tborder: 1px solid #ffb400;\r\n}\r\n.banner_social_link li a:hover{\r\n\tbackground: #ffb400 ;\r\n\tborder-color: #ffb400;\r\n\tcolor: #fff;\r\n}\r\n.dark_bg .banner_social_link li a:hover{\r\n\tbackground: #ffb400 ;\r\n\tborder: 1px solid #ffb400;\r\n\tcolor: #fff;\r\n}\r\n.banner-images img{\r\n\tmax-width: 100%;\r\n\tmargin-top: 40px;\r\n}\r\n#round {\r\n    position: absolute;\r\n    top: 75px;\r\n    bottom: 0;\r\n\tz-index: 9;\r\n}\r\n/* ----------------------------------------------------------------\r\n    [ End Banner ]\r\n-----------------------------------------------------------------*/\r\n\r\n\r\n/* ----------------------------------------------------------------\r\n    [ 15 Start Top-control ]\r\n-----------------------------------------------------------------*/\r\n.topcontrol {\r\n    background: #ffb400 none repeat scroll 0 0;\r\n    border-radius: 0;\r\n    bottom: 5px;\r\n    margin-bottom: 80px;\r\n    box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);\r\n    color: #fff;\r\n    cursor: pointer;\r\n    font-size: 20px;\r\n    height: 35px;\r\n    opacity: 1;\r\n    filter: alpha(opacity=100);\r\n    padding: 5px 9px;\r\n    position: fixed;\r\n    margin-right: 10px;\r\n    -webkit-transition: all 0.2s ease 0s;\r\n    transition: all 0.2s ease 0s;\r\n    width: 35px;\r\n    z-index: 99;\r\n}\r\n.topcontrol:hover {\r\n    background: #fff;\r\n    color: #ffb400;\r\n}\r\n/* ----------------------------------------------------------------\r\n    [ End Top-control ]\r\n-----------------------------------------------------------------*/"],"sourceRoot":""}]);
